:root {
    --WIDTH: 20%;
    --HEIGHT: 100%;
    --MARGIN_LEFT: 0%;
    --MARGIN_RIGHT: 80%;
    --MARGIN_TOP: 0%;
    --MARGIN_BOTTOM: 0%;
}

.ControlsWrapper {
    width: var(--WIDTH);
    height: var(--HEIGHT);
    margin-top: var(--MARGIN_TOP);
    margin-bottom: var(--MARGIN_BOTTOM);
    margin-left: var(--MARGIN_LEFT);
    margin-right: var(--MARGIN_RIGHT);

    background-color: black;
    float: left;
}

.ArrayLengthViewer {
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
    color: white;
    margin: 0.5rem 0 0.5rem 0;
}

/***************************/

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #04AA6D;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #04AA6D;
    cursor: pointer;
}